<template>
 <div class="changeBeCared">
   <div class="changeBeCaredHeader">
    <mt-header v-if="newBeCared">
      <router-link :to="{path: 'chooseBeCared', query: { clickType: 'index', serviceType: `${serviceType}`,id: `${id}`}}" slot="left">
        <mt-button icon="back">添加被护理人</mt-button>
      </router-link>
    </mt-header>
    <mt-header v-else>
      <router-link :to="{path: 'chooseBeCared', query: { clickType: 'index',serviceType: `${serviceType}`,id: `${id}` }}" slot="left">
        <mt-button icon="back">修改被护理人</mt-button>
      </router-link>
    </mt-header>
   </div>
   <div class="changeBeCaredBody">
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">被护理人</span>
          </div>
          <div class="w_cell_value">
            <span><input class="w_core" type="text" placeholder="被护理人姓名" v-model="name"></span>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">性别</span>
          </div>
          <div class="w_cell_value" style="border-bottom:none;">
            <label class="w_radiolist_label" style="margin-right:1.5rem;background:url('/static/male.png') no-repeat;background-position: right;padding-right: 0.5rem;    background-size: contain;">
              <span class="w_radio" >
                <input type="radio" v-model="sex" name="sex" class="w_radio_input" value="男">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label">男</span>
            </label>
            <label class="w_radiolist_label" style=";background:url('/static/female.png') no-repeat;background-position: right;padding-right: 0.5rem;background-size: contain;"> 
              <span class="w_radio">
                <input name="sex" v-model="sex" type="radio" class="w_radio_input" id="1" value="女">
                <span class="w_radio_core"></span>
              </span>
              <span class="w_radio_label">女</span>
            </label>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">电话</span>
          </div>
          <div class="w_cell_value">
            <span><input class="w_core" type="text" placeholder="请输入被护理人的电话" v-model="phone"></span>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">年龄</span>
          </div>
          <div class="w_cell_value">
            <span @click="showAgePopup = true">{{age}}</span>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">民族</span>
          </div>
          <div class="w_cell_value">
            <span @click="showNationPopup = true">{{nation}}</span>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">家属</span>
          </div>
          <div class="w_cell_value">
            <span><input class="w_core" type="text" placeholder="请输入家属的姓名" v-model="familyName"></span>
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">家属电话</span>
          </div>
          <div class="w_cell_value">
            <span><input class="w_core" type="text" placeholder="请输入家属的电话" v-model="familyPhone"></span>
          </div>
        </div>
      </a>
      <mt-popup v-model="showAgePopup" popup-transition="popup-fade">
          <div class="page-picker-wrapper">
            <mt-picker :slots="ageSlot" @change="onAgeChange" :visible-item-count="5"></mt-picker>
          </div>
      </mt-popup>
      <mt-popup v-model="showNationPopup" popup-transition="popup-fade">
          <div class="page-picker-wrapper">
            <mt-picker :slots="nationSlot" @change="onNationChange" :visible-item-count="5"></mt-picker>
          </div>
      </mt-popup>
   </div>
   <div class="footer">
     <mt-button size="large" @click.native="handleSubmitClick">提交</mt-button>
   </div> 
 </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import ageData from '../utils/maps/age'
import nationsObj from '../utils/maps/nationsMap';
const nationData = Object.keys(nationsObj);
import {sex, nation} from '../utils/maps';
import {check, checkPhone}  from '../utils/util';
import { Toast } from 'mint-ui';
export default {
  name: 'changeBeCared',
  data() {
    return {
      id: null,
      'newBeCared': true,
      showAgePopup: false,
      showNationPopup: false,
      ageSlot: [{
        flex: 1,
        values: ageData
      }],
      nationSlot: [{
        flex: 1,
        values: nationData
      }],
      age: '点击选择被护理人年龄',
      nation: '被护理人的民族',
      sex: null,
      name: null,
      phone: null,
      familyPhone: null,
      familyName: null,
      serviceType: null
    }
  },
  computed: {
    ...mapGetters({
      globalVal: 'getGlodalVal'
    })
  },
  methods: {
    ...mapActions({
        addBeCared: 'addBeCared',
        getBecaredDetail: 'getBecaredDetail',
        modifyBecared: 'modifyBecared'
      }),
    onAgeChange: function (picker, values) {
        this.age = values[0];
    },
    onNationChange: function (picker, values) {
      this.nation = values[0];
    },
    handleSubmitClick: function () {
      let obj = {};
      obj.name = this.name;
      obj.sex = sex.key(this.sex);
      obj.phone = this.phone;
      obj.age = this.age;
      obj.nation = nation.key(this.nation);
      obj.family = this.familyName;
      obj.familyPhone = this.familyPhone;
      obj.serviceType = this.serviceType;
      console.log(obj);
      const msgArr = ['请输入合法用户名', '请选择性别', '请输入正确的被护理人电话', '请选择正确年龄', '请输入正确的民族', '请输入家属的姓名', '请输入正确的家属电话'];
      check(obj, msgArr, function(err){
        Toast(err);
        return;
      });
      if(!Number(obj.age)) {
        Toast({
          message: '请选择正确年龄',
          position: 'bottom',
          duration: 1000
        })
        return;
      }
      if(!checkPhone(obj.phone)) {
        Toast({
          message: '请输入正确的被护理人电话',
          position: 'bottom',
          duration: 1000
        })
        return;
      }
      if(!checkPhone(obj.familyPhone)) {
        Toast({
          message: '请输入正确的家属电话',
          position: 'bottom',
          duration: 1000
        })
        return;
      }
      if(this.newBeCared) {
        this.addBeCared(obj).then(response => {
          this.$router.push({path: '/chooseBeCared', query: { clickType: 'index' }})
        })
      } else {
        obj.id = this.id;
        this.modifyBecared(obj).then(response => {
          this.$router.push({path: '/chooseBeCared', query: { clickType: 'index' }})
        })
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    if(to.query.new == 'true') {
      next(vm => {
        vm.newBeCared = true;
        vm.serviceType = to.query.serviceType;
        vm.id = to.query.id;
      })
    } else {
      next(vm => {
          vm.newBeCared = false;
          vm.serviceType = to.query.serviceType;
          vm.id = to.query.id;
      })
      //console.log(this.getBecaredDetail)
      // this.getBecaredDetail(to.query.id).then(response => {
      //   console.log(response.data);
      //   next(vm => {
      //     vm.newBeCared = false;
      //     vm.serviceType = to.query.serviceType;
      //     vm.id = to.query.id;
      //   })
      // },error => {
      //   Toast({
      //     message: '网路出错',
      //     position: 'bottom',
      //     duration: 1000
      //   })
      // })
    }
  },
  mounted() {
    //this.getBecaredDetail()
    if(!this.globalVal.becaredStatus) {
      this.getBecaredDetail(this.globalVal.beCaredId).then(response => {
      console.log(response.data);
      this.age = response.data.age;
      this.familyName = response.data.family;
      this.familyPhone = response.data.familyPhone;
      this.name = response.data.name;
      this.nation = nation.val(response.data.nation);
      this.phone = response.data.phone;
      this.sex = sex.val(response.data.sex);
    })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mint-header {
  background-color: #fff;
  color: black;
  margin-bottom: 3%;
}
.radiolistTitle {
  font-size: 12px;
  margin: 8px;
  display: block;
  color: #888;
}
.mint-popup {
  width: 40%;
}
.mint-button {
  background-color: #fff;
  border: none;
}
.mint-button--default {
  box-shadow: none;
}
.mint-radiolist {
  display: flex;
}
.mint-cell::before {
  content: '';
  width: 0;
}
.mint-cell::after {
  content: '';
}
.footer {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
}
.footer .mint-button {
  background: #fff;
  color: #f1cfde;
  border-top: 3px solid #f1cfde;
}
</style>



